/**
 * 联系人分区数据。
 */
 export class ContactZoneParticipant {

    constructor(json) {
        /**
         * 联系人的 ID 。
         * @type {number}
         */
        this.id = json.id;

        /**
         * 参与者的状态。
         * @type {number}
         */
        this.state = json.state;

        /**
         * 分区所有人留给该参与者的附言。
         * @type {string}
         */
        this.postscript = '';

        if (undefined !== json.postscript) {
            this.postscript = json.postscript;
        }
    }
 }
